
.rrd{
    margin-top: 100px;
    display: flex;
    height: fit-content;
    background-color: black;
}

.rrd-left{
    width: 30%;
    padding: 60px;
}
.rrd-left img{
    width: calc(100% - 20px);
}
.rrd-left p{
    color: white;
}

.rrd-right{
    width: 70%;
}
.rrd-right img{
    width: 100%;
    object-fit: cover;
}


.rrd-left button{
    /* background-color: #1e8fff4a; */
    font-weight:bold;
    background-color: black ;
    border-color: white;
    color:white;
    padding: 10px 20px;
    text-align: center;
    /* text-decoration: none; */
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
    border-radius: 5px;
    transition: all 0.3s ease;
  
}

.rrd-left button:hover{
    background-color: var(--Mycolor);
    color: rgb(0, 0, 0);
    border-color:var(--Mycolor);
     box-shadow: 0 0 10px rgba(255, 255, 255, 0.3);
}